import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, reset } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { FormModal } from "../../../../actions/databarang_action";
import { ReanderField } from "../../../../components/helpers/field";
import { getItem, setItem } from "../../../../components/helpers/function";
import { getData2 } from "../../../../config/axios";
import HeadFormBerlian from "./HeadFormBerlian";
import { showEditBarang } from './../../../../actions/databarang_action';
const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "id-ID",
});
const maptostate = (state) => {
  return {
    hideModal: state.datamaster.modalDialog,
    databarang : state.databarang.showEditBarang
  };
};
class ModalBerlian extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      disabled : true
    };
  }

  handleSubmit(data) {
    this.setState({ isLoading: true });
    setTimeout(() => {
      let cek = JSON.parse(localStorage.getItem("data_batu")) || [];
      let hasil_cek = cek.findIndex((hasil) => hasil.kode_barcode === "");
      if (hasil_cek === -1) {
        cek.push(data);
        setItem("data_batu", cek);
        this.props.dispatch(reset('HeadFormBerlian'))
      }
      this.setState({ isLoading: false });
    }, 1000);
  }
  componentDidMount() {
    this.props.change("total_usd", getItem("total_usd"));
    this.props.change("total_rp", getItem("total_rp"));
    let hasil = getItem("total_rp") || 0;
    if(parseInt(hasil) >0 ) {
        this.setState({
            disabled : false,
        })
      }else{
        this.setState({
            disabled : true,
        })
      }
  }
  getRp(value) {
    getData2("para-system/key/CURRENCY_DOLLAR_RUPIAH").then((res) => {
      let hasil = parseInt(value || 0) * parseInt(res.data.value || 0);
      setItem("total_usd", parseInt(value || 0));
      setItem("total_rp", parseInt(hasil || 0));
      this.props.change("total_rp", hasil);
      if(parseInt(hasil) >0 ) {
        this.setState({
            disabled : false,
        })
      }else{
        this.setState({
            disabled : true,
        })
      }
    }).catch((err)=>{
      
    })
  }
  render() {
    return (
      <>
        <HeadFormBerlian
          isLoading={this.state.isLoading}
          onSubmit={(data) => this.handleSubmit(data)}
        />
        <form
          onSubmit={this.props.handleSubmit}
          autoComplete="off"
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
        >
          <div className="row">
            <div className="col-6"></div>
            <div className="col-3">
              <Field
                name="total_usd"
                component={ReanderField}
                label="Total Usd"
                onChange={(e) => this.getRp(e.target.value)}
                placeholder="Masukan Total Usd"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
              />
            </div>
            <div className="col-3">
              <Field
                name="total_rp"
                component={ReanderField}
                label="Total Rp"
                {...currencyMask}
                readOnly
                placeholder="Masukan Total Rp"
              />
            </div>
            <div className="col-6"></div>
            <div className="col-6 text-right">
              <button
                disabled={this.state.disabled}
                type="button"
                onClick={() =>
                 {
                    this.props.dispatch(showEditBarang(this.props.databarang))
                    this.props.dispatch(FormModal("FormTambahBarang"))
                 }
                }
                className="btn btn-secondary mt-4"
              >
                {" "}
                Kembali{" "}
              </button>
              &nbsp;
            </div>
          </div>
        </form>
      </>
    );
  }
}

ModalBerlian = reduxForm({
  form: "ModalBerlian",
  enableReinitialize: true,
})(ModalBerlian);
export default connect(maptostate)(ModalBerlian);
