import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import {
  getDataCutgrad,
  getJenisBatu,
  getDataShape,
  getDataPolish,
  getDataSymetry,
  getDataFluorencence,
  getBatuColor,
  getDataClarity,
} from "../../../../actions/datamaster_action";
import {
  ReanderField,
  ReanderSelect,
} from "../../../../components/helpers/field";
import { getItem } from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import { Gramasi, NumberOnly } from "../../../../Validasi/normalize";

const maptostate = (state) => {
  return {
    getJenisBatu: state.datamaster.getJenisBatu,
    getDataCutgrad: state.datamaster.getDataCutgrad,
    getDataShape: state.datamaster.getDataShape,
    getDataPolish: state.datamaster.getDataPolish,
    getDataFluorencence: state.datamaster.getDataFluorencence,
    getDataSymetry: state.datamaster.getDataSymetry,
    getDataClarity: state.datamaster.getDataClarity,
    getBatuColor: state.datamaster.getBatuColor,
  };
};
class HeadFormBerlian extends Component {
  constructor(props) {
    super(props);
    this.state = {
        berhasil : false,
      columns: [
        {
          dataField: "jenis_batu",
          text: "Jenis Batu",
        },
        {
          dataField: "jumlah",
          text: "Jumlah",
        },
        {
          dataField: "karat_batu",
          text: "Karat Batu",
        },
        {
          dataField: "shape",
          text: "Shape",
        },
        {
          dataField: "color",
          text: "Color",
        },
        {
          dataField: "clarity",
          text: "Clarity",
        },
        {
          dataField: "size",
          text: "Size",
        },
        {
          dataField: "polish",
          text: "Polish",
        },
        {
          dataField: "symmetry",
          text: "Symmetry",
        },
        {
          dataField: "fluorescence",
          text: "Fluorescence",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    onClick={() => this.hapusData(index)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
  }
  hapusData(index) {
    let data = JSON.parse(localStorage.getItem("data_batu"));
    data.splice(index, 1);
    localStorage.setItem("data_batu", JSON.stringify(data));
    this.setState({ berhasil: true });
  }
  componentDidMount() {
    this.props.dispatch(getJenisBatu());
    this.props.dispatch(getDataCutgrad());
    this.props.dispatch(getDataShape());
    this.props.dispatch(getDataPolish());
    this.props.dispatch(getBatuColor());
    this.props.dispatch(getDataSymetry());
    this.props.dispatch(getDataClarity());
    this.props.dispatch(getDataFluorencence());
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <div className="row">
          <div className="col-3">
            <Field
              name="jenis_batu"
              component={ReanderSelect}
              options={this.props.getJenisBatu.map((list) => {
                let row = {
                  name: list.jenis_batu,
                  value: list.jenis_batu,
                };
                return row;
              })}
              label="Jenis Batu"
              placeholder="SILAHKAN PILIH JENIS BATU"
            />
          </div>
          <div className="col-3">
            <Field
              name="jumlah"
              component={ReanderField}
              label="Jumlah"
              placeholder="MASUKAN JUMLAH"
              normalize={NumberOnly}
            />
          </div>
          <div className="col-3">
            <Field
              name="karat_batu"
              component={ReanderField}
              label="Karat Batu"
              placeholder="MASUKAN KARAT BATU"
              normalize={Gramasi}
            />
          </div>
          <div className="col-3">
            <Field
              name="size"
              component={ReanderField}
              label="Size"
              placeholder="MASUKAN SIZE"
            />
          </div>
          <div className="col-3">
            <Field
              name="cute_grade"
              component={ReanderSelect}
              options={this.props.getDataCutgrad.map((list) => {
                let row = {
                  value: list.kode_cutgrade,
                  name: list.kode_cutgrade,
                };
                return row;
              })}
              label="Cut Grade"
              placeholder="SILAHKAN PILIH CUT GRADE"
            />
          </div>
          <div className="col-3">
            <Field
              name="shape"
              component={ReanderSelect}
              options={this.props.getDataShape.map((list) => {
                let row = {
                  value: list.kode_shape,
                  name: list.kode_shape,
                };
                return row;
              })}
              label="Shape"
              placeholder="SILAHKAN PILIH SHAPE"
            />
          </div>
          <div className="col-3">
            <Field
              name="polish"
              component={ReanderSelect}
              options={this.props.getDataPolish.map((list) => {
                let row = {
                  value: list.kode_polish,
                  name: list.kode_polish,
                };
                return row;
              })}
              label="Polish"
              placeholder="SILAHKAN PILIH POLISH"
            />
          </div>
          <div className="col-3">
            <Field
              name="color"
              component={ReanderSelect}
              options={this.props.getBatuColor.map((list) => {
                let row = {
                  value: list.kode_color,
                  name: list.kode_color,
                };
                return row;
              })}
              label="Color"
              placeholder="SILAHKAN PILIH COLOR"
            />
          </div>
          <div className="col-3">
            <Field
              name="symmetry"
              component={ReanderSelect}
              options={this.props.getDataSymetry.map((list) => {
                let row = {
                  value: list.kode_symmetry,
                  name: list.kode_symmetry,
                };
                return row;
              })}
              label="Symmetry"
              placeholder="SILAHKAN PILIH SYMMETRY"
            />
          </div>
          <div className="col-3">
            <Field
              name="clarity"
              component={ReanderSelect}
              options={this.props.getDataClarity.map((list) => {
                let row = {
                  value: list.kode_clarity,
                  name: list.kode_clarity,
                };
                return row;
              })}
              label="Clarity"
              placeholder="SILAHKAN PILIH CLARITY"
            />
          </div>
          <div className="col-3">
            <Field
              name="fluorescence"
              component={ReanderSelect}
              options={this.props.getDataFluorencence.map((list) => {
                let row = {
                  value: list.kode_fluorescence,
                  name: list.kode_fluorescence,
                };
                return row;
              })}
              label="Flourescence"
              placeholder="SILAHKAN PILIH FLOURESCENCE"
            />
          </div>
          <div className="col-3">
            <button
              disabled={this.props.isLoading}
              type="submit"
              className="btn btn-primary btn-block mt-4"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Tambah Data
                </>
              ) : (
                "Tambah Data"
              )}
            </button>
          </div>

          <div className="col-12">
            <Tabel
              keyField="kode_barcode"
              data={getItem("data_batu" || [])}
              columns={this.state.columns}
              empty={true}
              exportCsv={false}
            />
          </div>
        </div>
      </form>
    );
  }
}

HeadFormBerlian = reduxForm({
  form: "HeadFormBerlian",
  enableReinitialize: true,
})(HeadFormBerlian);
export default connect(maptostate)(HeadFormBerlian);
