import {
  React,
  Component,
  Camera,
  HiidenFiled,
  ReanderField,
  createNumberMask,
  Field,
  reduxForm,
  connect,
  showModal,
  ToastNotification,
  getData,
  Gramasi,
  imgfound,
  hideModal,
  formValueSelector,
  NumberOnly,
} from "../../../../components/helpers/library";
import { getData2, postData2 } from "../../../../config/axios";
import ValidasiFilterTambahBarang from "../../../../Validasi/Barang/TambahBarang/ValidasiFilterTambahBarang";
import { setCamera } from "../Module";
import Loading from "react-fullscreen-loading";
import { Kadar } from "../../../../Validasi/normalize";
import {
  dataURLtoFile,
  getItem,
  resizeFile,
  setItem,
  getUserdata,
} from "../../../../components/helpers/function";
import { io } from "socket.io-client";
// import { getTimbangan } from "../../../../GetTimbangan";
// import { getTimbangan } from "../../../../actions/utility_action";
const server = process.env.REACT_APP_BACKEND_URL;

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "id-ID",
});

class FormTambahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: true,
      isBaki: true,
      jml: 1,
      disabled: false,
      ModalWebCam: false,
      module_berlian: false,
      module_semi_grosir: false,
      loadimage: imgfound,
      isLoadingBaki: false,
      isLoadingJenis: false,
      kategori_harga: "",
      tgl_awal: new Date(),
      tgl_akhir: new Date(),
      databaki: [],
      DataGroup: [],
      DataJenis: [],
    };
  }
  tgl_awalChange = (date) => {
    this.setState({
      tgl_awal: date,
    });
  };
  tgl_akhirChange = (date) => {
    this.setState({
      tgl_akhir: date,
    });
  };
  showModalWebcam() {
    this.props.dispatch(setCamera(false));
    this.props.dispatch(showModal());
    this.setState({ ModalWebCam: true });
  }

  async handleTakePhoto(url) {
    try {
      document.getElementById("kode_intern").focus();
      const file = dataURLtoFile(url);
      const res = await resizeFile(file);
      // console.log(res);
      this.props.dispatch(setCamera(res));
      this.props.change("foto", res);
    } catch (err) {
      // console.log(err);
    }
  }
  async componentWillUnmount() {
    this.setState({
      isBaki: false,
    });
    // clearInterval(this.interval1);
    localStorage.removeItem("estimasi_harga_tambah");
    // if (this.reader) {
    //   await this.reader.cancel();
    //   // await this.inputDone.catch(() => {});
    //   this.reader = null;
    //   this.inputDone = null;
    //   // console.log("1");
    // }
    // if (this.outputStream) {
    //   await this.outputStream.getWriter().close();
    //   await this.outputDone;
    //   this.outputStream = null;
    //   this.outputDone = null;
    //   // console.log("2");
    // }
    // // await port.close();
    // if (this.port) {
    //   // console.log("3");
    //   await this.port.close();
    //   this.port = null;
    //   return false;
    // }
  }

  componentDidMount() {
    const module = getItem("module") || [];
    let module_berlian = module.findIndex((list) => list.key === "BERLIAN_MODULE");
    let module_semi_grosir = module.findIndex((list) => list.key === "SEMI_GROSIR_MODULE");
    if(module_semi_grosir === -1){
      this.setState({
        module_semi_grosir: false
      })
    }else{
      this.setState({
        module_semi_grosir: true
      })
    }
    if(module_berlian === -1){
      this.setState({
        module_berlian: false
      })
    }else{
      this.setState({
        module_berlian: true
      })
    }
    let datagroup = getItem("data_group");
    if(datagroup.is_berlian === false || datagroup.is_berlian === undefined){
      this.setState({
        module_berlian: false
      })
    }else{
      this.setState({
        module_berlian: true
      })
    }
    this.hitungOngkosBerlian()
    getData2("para-system/key/kategori_harga")
        .then((res) => {
          // console.log(res.data.value);
          if (res.data.value === "harga_100%") {
            this.setState({
              kategori_harga: true,
            });
          } else {
            this.setState({
              kategori_harga: false,
            });
          }

          this.props.change("kadar", this.state.kategori_harga ? this.props.DataBarang.modal_berlian ==="MODAL_BERLIAN" ? this.props.DataBarang.kadar : 0 : 100);
          this.props.change("kadar_modal", this.state.kategori_harga ? this.props.DataBarang.modal_berlian ==="MODAL_BERLIAN" ? this.props.DataBarang.kadar_modal : 0 : 100);
        })
        .catch((err) => {
          console.log(err);
        });

        const socket = io.connect(server.replace("/api/v1/", ""));
        socket.on("timbangan", (data) => {
          if (getUserdata().user_id === data.user_id) {
            this.props.change("berat_asli", data.value);
          }
        });
    if(this.props.DataBarang.modal_berlian === "MODAL_BERLIAN"){
      setTimeout(() => {
        document.getElementById('ongkos_berlian').focus();
      }, 100);
      this.hitungOngkosBerlian()
      this.props.change("total_harga", this.props.total_harga);
    }
    if(this.props.DataBarang.modal_berlian !== "MODAL_BERLIAN"){
      localStorage.removeItem("estimasi_harga_tambah");

      this.props.dispatch(setCamera(false));
      this.props.change("foto", "");

      getData2("para-system/key/PEMBULATAN")
        .then((res) => {
          setItem("pembulatan", res.data.value);
        })
        .catch((err) => {
          // console.log(err);
          setItem("pembulatan", 500);
        });

      let kode = this.props.DataBarang.kode_lokasi_toko.split("~");
      this.props.change("kode_kategori", this.props.DataBarang.kode_kategori);
      this.props.change("kode_jenis", this.props.DataBarang.kode_jenis);
      this.props.change("kode_lokasi_toko", kode[0]);
      this.props.change("kode_gudang", kode[1]);
      this.props.change("kode_intern", this.props.DataBarang.kode_intern);
      this.props.change("kode_barcode", this.props.DataBarang.kode_barcode);
      this.props.change("nama_barang", this.props.DataBarang.nama_barang);
      this.props.change("ongkos", this.props.DataBarang.ongkos);
      this.props.change("kadar",
        this.state.kategori_harga ? this.props.DataBarang.kadar : 100
      );
      this.props.change("kadar_cetak", this.props.DataBarang.kadar_cetak);
      this.props.change("kadar_modal", this.props.DataBarang.kadar_modal);
      this.props.change("nama_atribut", this.props.DataBarang.nama_atribut);
      this.props.change("berat_atribut", this.props.DataBarang.berat_atribut);
      this.props.change("berat_bandrol", this.props.DataBarang.berat_bandrol);
      this.props.change("harga_atribut", this.props.DataBarang.harga_atribut);
      this.props.change("foto", this.props.DataBarang.foto);

      this.props.change("berat_stock", getItem("saldo_supplier"));

      this.props.change("tag_id", this.props.DataBarang.tag_id || "-");

      setTimeout(() => {
        getData("parabandrol/get/all").then((res) => {
          if (res.data.length !== 0) {
            this.props.change("berat_bandrol", res.data[0].berat_bandrol);
          } else {
            this.props.change("berat_bandrol", 0);
          }
        });
        document.getElementById("kode_intern").focus();
      }, 100);
    }

  }

  getValue = async (event) => {
    try {
      document.getElementById("kode_intern").focus();
      const file = event.target.files[0];
      const res = await resizeFile(file);
      // console.log(res);
      this.props.dispatch(setCamera(res));
      this.props.change("foto", res);
    } catch (err) {
      // console.log(err);
    }
  };

  getJenis(params) {
    this.setState({ isLoadingJenis: true });
    getData("jenis/get/by-kode-kategori/" + params)
      .then((res) => {
        this.setState({
          DataJenis: res.data,
        });
      })
      .then((res) => {
        this.setState({
          isLoadingJenis: false,
        });
      })
      .catch((err) => {
        ToastNotification("info", err.data);
        console.log(err);
      });
  }
  getBaki(params) {
    this.setState({ isLoadingBaki: true });
    getData("baki/get/by-kode-baki/" + params)
      .then((res) => {
        this.setState({
          databaki: res.data === "" ? [] : [res.data],
        });
      })
      .then((res) => {
        this.setState({
          isLoadingBaki: false,
        });
      })
      .catch((err) => {
        ToastNotification("info", err.data);
        console.log(err);
      });
  }
  loadcamera() {
    return (
      <Camera
        onTakePhoto={(dataUri) => {
          this.handleTakePhoto(dataUri);
        }}
      />
    );
  }
  cekTotal() {
    this.props.change("harga_jual", this.props.harga_jual);
    this.hitungOngkosBerlian()
    if (this.props.status === "PESANAN") {
      let row = {
        kode_group: this.props.kode_group,
        berat: parseFloat(document.getElementById("berat").value || 0),
        kadar: parseInt(document.getElementById("kadar").value || 0),
      };
      postData2("tambah-barang/calculate", row).then((res) => {
        // console.log(res.data.harga_jual + this.props.harga_atribut + this.props.ongkos);
        // console.log(this.props.harga_atribut);
        let harga = getItem("estimasi_harga_tambah") || [];
        // console.log(harga.length);
        if (harga.length === 0) {
          setItem(
            "estimasi_harga_tambah",
            res.data.harga_jual + this.props.harga_atribut + this.props.ongkos
          );
          this.props.change("harga_estimasi", getItem("estimasi_harga_tambah"));
        }
        this.props.change(
          "harga_total",
          res.data.harga_jual + this.props.harga_atribut + this.props.ongkos
        );
        this.props.change("harga_total_tmp", res.data.harga_jual);
      });
    }
    // console.log(e);
  }
  hitungCalculate() {
    if (this.props.status === "PESANAN") {
      this.props.change("harga_total", this.props.harga_total);
    }
  }

  cekBerat(e) {
    if (getItem("saldo_supplier")) {
      let berat_stock = getItem("saldo_supplier");
      if (parseFloat(e.target.value || 0) > parseFloat(berat_stock)) {
        ToastNotification("info", "Berat Yang Diinputkan Melebihi Berat Stock");
        this.setState({
          disabled: true,
        });
        return false;
      } else {
        this.setState({
          disabled: false,
        });
      }
    }
  }
  hitungOngkosBerlian() {
    // console.log(this.props.harga_total_tanpa_profit);
    this.props.change("total_harga", this.props.total_harga);
  }
  render() {
   
    return (
      <>
        <form
          onSubmit={this.props.handleSubmit}
          autoComplete="off"
          onKeyPress={(e) => {
            e.key === "Enter" && e.preventDefault();
          }}
        >
          <div className="row">
            <div className="col-lg-12 text-center">
              {this.state.ModalWebCam === true ? (
                this.props.setCamera === false ? (
                  <div className="container">{this.loadcamera()}</div>
                ) : (
                  <>
                    <img
                      src={
                        this.props.foto === undefined
                          ? this.props.setCamera === false
                            ? imgfound
                            : this.props.setCamera
                          : this.props.setCamera
                          ? this.props.setCamera
                          : this.props.foto
                      }
                      alt="img"
                      height="245px"
                      width="355px"
                    />
                  </>
                )
              ) : (
                <>
                  <img
                    src={
                      this.props.foto === undefined
                        ? this.props.setCamera === false
                          ? imgfound
                          : this.props.setCamera
                        : this.props.setCamera
                        ? this.props.setCamera
                        : this.props.foto
                    }
                    alt="img"
                    height="245px"
                    width="355px"
                  />
                </>
              )}
            </div>
          </div>
          <br />
          <div className="row">
            <div className="col-2"> </div>
            <div className="col-5">
              <div className="input-group">
                <div className="custom-file">
                  <input
                    tabIndex="1"
                    type="file"
                    name="photo"
                    autoComplete="off"
                    className="custom-file-input"
                    id="exampleInputFile"
                    onChange={(e) => this.getValue(e)}
                  />
                  <label
                    className="custom-file-label"
                    htmlFor="exampleInputFile"
                  >
                    Pilih gambar
                  </label>
                </div>
              </div>
            </div>
            <div className="col-3">
              <button
                tabIndex="2"
                className="btn btn-block btn-primary"
                type="button"
                onClick={() => this.showModalWebcam()}
              >
                {" "}
                WebCam{" "}
              </button>
            </div>
            <hr />
          </div>
          <hr />
          <div className="row">
            <div className="col-lg-4">
              <Field
                readOnly={true}
                id="kode_kategori"
                name="kode_kategori"
                component={ReanderField}
                label="Kategori"
                placeholder="Masukan Kategori"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
              />
            </div>
            <div className="col-lg-4">
              <Field
                readOnly={true}
                name="kode_jenis"
                component={ReanderField}
                label="Jenis"
                placeholder="Masukan Jenis"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
              />
            </div>
            <div className="col-lg-4">
              <Field
                readOnly={true}
                name="kode_lokasi_toko"
                component={ReanderField}
                label="Kode Lokasi Toko"
                placeholder="Masukan Lokasi"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
              />
            </div>
          </div>
          <hr />
          <div className="row">
              <Field
                readOnly={true}
                id="ongkos"
                name="ongkos"
                type="hidden"
                component={HiidenFiled}
              />
              <Field
                readOnly={true}
                name="harga_total_tmp"
                type="hidden"
                component={HiidenFiled}
              />
              <Field
                readOnly={true}
                name="status_pesanan"
                type="hidden"
                component={HiidenFiled}
              />
              <Field
                readOnly={true}
                name="kode_gudang"
                type="hidden"
                component={HiidenFiled}
              />
              <Field
                readOnly={true}
                name="foto"
                type="hidden"
                component={HiidenFiled}
              />
              <Field
                readOnly={true}
                name="kode_barcode"
                type="hidden"
                component={HiidenFiled}
              />

            <div className="col-6">
              <Field
                  id="kode_intern"
                  tabIndex="3"
                  onFocus={(e) => this.cekTotal()}
                  name="kode_intern"
                  component={ReanderField}
                  label="Kode Intern"
                  placeholder="Masukan Kode Intern"
                  onClick={(e) => e.target.select()}
                />
            </div>
            <div className="col-lg-6">
            </div>
            <div
              className={
                this.props.status === "PESANAN" ? "col-6" : "col-6 d-none"
              }
            >
              <Field
                id="no_pesanan"
                tabIndex="3"
                name="no_pesanan"
                component={ReanderField}
                readOnly={true}
                label="No Pesanan"
                placeholder="Masukan No Pesanan"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
              />
            </div>
            <div className="col-12">
              <Field
                tabIndex="4"
                name="nama_barang"
                component={ReanderField}
                label="Nama Barang"
                placeholder="Masukan Nama Barang"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
              />
            </div>
            <div className="col-lg-6">
              {this.props.status === "PESANAN" ? (
                <>
                  <Field
                    tabIndex="5"
                    type="text"
                    name="berat_awal"
                    component={ReanderField}
                    // readOnly={this.props.status === "PESANAN" ? true : false}
                    label="Berat Awal"
                    normalize={Gramasi}
                    readOnly={true}
                    placeholder="Masukan Berat Awal"
                    onFocus={(e) => e.target.select()}
                    onClick={(e) => e.target.select()}
                    onBlur={(e) => this.props.change("berat", e.target.value)}
                  />
                </>
              ) : (
                <>
                  <Field
                    tabIndex="5"
                    type="text"
                    id="berat_asli"
                    name="berat_asli"
                    component={ReanderField}
                    onChange={(e) => this.cekBerat(e)}
                    // readOnly={this.props.status === "PESANAN" ? true : false}
                    label="Berat Asli"
                    normalize={Gramasi}
                    placeholder="Masukan Berat Asli"
                    onFocus={(e) => e.target.select()}
                    onClick={(e) => e.target.select()}
                    onBlur={(e) => this.props.change("berat", e.target.value)}
                  />
                </>
              )}
            </div>
            <div className="col-lg-6">
              <Field
                id="berat"
                tabIndex="6"
                type="text"
                name="berat"
                component={ReanderField}
                label={this.props.status === "PESANAN" ? "Berat Jadi" : "Berat"}
                normalize={Gramasi}
                placeholder={
                  this.props.status === "PESANAN"
                    ? "Masukan Berat Jadi"
                    : "Masukan Berat"
                }
                onChange={(e) => this.cekBerat(e)}
                onFocus={(e) => this.cekTotal()}
                onClick={(e) => e.target.select()}
                onBlur={() => this.cekTotal()}
              />
            </div>
            <div
              className={
                this.state.kategori_harga ? "col-lg-6" : "col-lg-6 d-none"
              }
            >
              <Field
                id="kadar"
                tabIndex="7"
                type="text"
                name="kadar"
                component={ReanderField}
                label="Kadar"
                normalize={NumberOnly}
                placeholder="Masukan Kadar"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
                onBlur={() => this.cekTotal()}
                onChange={this.cekTotal()}
              />
            </div>
            <div className="col-lg-6">
              <Field
                tabIndex="8"
                type="text"
                name="kadar_cetak"
                component={ReanderField}
                label="Kadar Cetak"
                normalize={Kadar}
                placeholder="Masukan Kadar Cetak"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
              />
            </div>

            <div
              className={
                this.state.kategori_harga === false
                  ? "d-none"
                  : "col-lg-6 d-block"
              }
            >
              <Field
                tabIndex="9"
                type="text"
                name="kadar_modal"
                normalize={Kadar}
                component={ReanderField}
                label="Kadar Modal"
                placeholder="Masukan Kadar Modal"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
              />
            </div>

            {this.state.module_semi_grosir &&(
              <>
                <div className="col-lg-6">
                  <Field
                    tabIndex="10"
                    type="text"
                    name="berat_stock"
                    readOnly
                    normalize={Gramasi}
                    component={ReanderField}
                    label="Berat Stock"
                    placeholder="Masukan Berat Stock"
                    onFocus={(e) => e.target.select()}
                    onClick={(e) => e.target.select()}
                  />
                </div>
              </>
            )}

            {this.state.module_berlian && (
              <div className={this.state.module_semi_grosir ===true ? "col-lg-12" : "col-lg-6"}>
                <Field
                  type="text"
                  name="harga_jual"
                  readOnly
                  {...currencyMask}
                  component={ReanderField}
                  label="Harga Jual"
                  placeholder="Masukan Harga Jual"
                />
              </div>
            )}
          </div>

          <hr />
          <div className="row">
            <div className="col-12">
              <Field
                id="nama_atribut"
                tabIndex="10"
                type="text"
                name="nama_atribut"
                component={ReanderField}
                label="Nama Atribut"
                placeholder="Masukan Nama Atribut"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
              />
            </div>
            <div className="col-lg-6">
              <Field
                tabIndex="11"
                type="text"
                name="berat_atribut"
                component={ReanderField}
                label="Berat Atribut"
                normalize={Gramasi}
                placeholder="Masukan Berat Atribut"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
              />
            </div>
            <div className="col-lg-6">
              <Field
                tabIndex="12"
                id="harga_atribut"
                name="harga_atribut"
                component={ReanderField}
                {...currencyMask}
                label="Harga Atribut"
                placeholder="Masukan Harga Atribut"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
                onChange={this.hitungCalculate()}
              />
            </div>
            <div className="col-lg-6">
              <Field
                tabIndex="13"
                type="text"
                name="berat_plastik"
                component={ReanderField}
                label="Berat Plastik"
                normalize={Gramasi}
                placeholder="Masukan Berat Plastik"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
              />
            </div>
            {this.props.status === "PESANAN" ? (
              <>
                <div className="col-lg-6">
                  <Field
                    tabIndex="13"
                    type="text"
                    name="harga_estimasi"
                    component={ReanderField}
                    label="Harga Estimasi"
                    // normalize={Gramasi}
                    {...currencyMask}
                    placeholder="Harga Estimasi"
                    readOnly={true}
                  />
                </div>
                <div className="col-6"></div>
                <div className="col-6">
                  <Field
                    tabIndex="13"
                    type="text"
                    name="harga_total"
                    component={ReanderField}
                    label="Harga Total"
                    // normalize={Gramasi}
                    {...currencyMask}
                    placeholder="Harga Total"
                    readOnly={true}
                  />
                </div>
              </>
            ) : null}
          </div>
          <hr />
          <div className="row">
            <div className="col-lg-6 d-none">
              <Field
                tabIndex="13"
                name="tag_id"
                component={HiidenFiled}
                type="hidden"
                label="Tag Id"
                placeholder="Masukan Tag Id"
              />
            </div>
            <div className="col-lg-6 d-none">
              <Field
                tabIndex="14"
                type="text"
                name="berat_bandrol"
                component={ReanderField}
                label="Berat Bandrol"
                normalize={Gramasi}
                placeholder="Masukan Berat Bandrol"
                onFocus={(e) => e.target.select()}
                onClick={(e) => e.target.select()}
              />
            </div>
          </div>
          {this.state.module_berlian && (
            <>
              <div className="row">
                
                <div className="col-lg-6">
                  <Field
                    tabIndex="15"
                    type="text"
                    name="no_seri"
                    component={ReanderField}
                    label="No Seri"
                    placeholder="Masukan No Seri"
                    onFocus={(e) => e.target.select()}
                    onClick={(e) => e.target.select()}
                    onBlur={()=>{document.getElementById('ongkos_berlian').focus()}}

                  />
                </div>
                <div className="col-lg-6">
                  <Field
                    tabIndex="16"
                    type="text"
                    id="ongkos_berlian"
                    name="ongkos_berlian"
                    component={ReanderField}
                    label="Ongkos Berlian"
                    readOnly={
                      parseInt(getItem("total_rp") || 0) > 0 ? false : true
                    }
                    {...currencyMask}
                    placeholder="Masukan Ongkos Berlian"
                    onFocus={(e) => this.hitungOngkosBerlian()}
                    onClick={(e) => this.hitungOngkosBerlian()}
                    onChange={(e) => this.hitungOngkosBerlian()}
                    onBlur={(e) => this.hitungOngkosBerlian()}
                  />
                </div>
                <div className="col-lg-6">
                  <Field
                    onFocus={(e) => this.hitungOngkosBerlian()}
                    onClick={(e) => this.hitungOngkosBerlian()}
                    onChange={this.hitungOngkosBerlian()}
                    onBlur={(e) => this.hitungOngkosBerlian()}
                    tabIndex="17"
                    type="text"
                    name="profit"
                    component={ReanderField}
                    label="Profit"
                    normalize={NumberOnly}
                    placeholder="Masukan Profit"
                  />
                </div>
                <div className="col-lg-6">
                  <Field
                    tabIndex="18"
                    type="text"
                    name="total_harga"
                    component={ReanderField}
                    label="Total Harga"
                    {...currencyMask}
                    readOnly
                    placeholder="Masukan Total Harga"
                    onFocus={(e) => e.target.select()}
                    onClick={(e) => e.target.select()}
                  />
                </div>
              </div>
              <hr />
            </>
          )}

          <div className="row">
            <div className="col-6">
              {this.state.module_berlian && (
                <>
                  <button
                    // tabIndex="14"
                    onClick={() =>
                      this.props.modalBerlian(this.props.databarangs)
                    }
                    type="button"
                    className="btn btn-primary"
                  >
                    Data Berlian
                  </button>
                </>
              )}
            </div>
            <div className="col-lg-6 text-right">
              {/* <div className="form-group">
                <label> &nbsp; </label> */}
              <button
                // tabIndex="14"
                onClick={() => this.props.dispatch(hideModal())}
                type="button"
                className="btn btn-secondary"
              >
                Batal
              </button>
              &nbsp;
              <button
                tabIndex="15"
                disabled={this.props.isLoading || this.state.disabled}
                type="submit"
                className="btn btn-success"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Menyimpan
                  </>
                ) : (
                  "Simpan"
                )}
              </button>
            </div>
          </div>
          {/* </div> */}
          {this.props.isLoading ? (
            <Loading
              loading
              background="rgba(0, 0, 0, 0.35)"
              loaderColor="rgba(94, 147, 117, 1)"
            />
          ) : null}
        </form>
      </>
    );
  }
}

FormTambahBarang = reduxForm({
  form: "FormTambahBarang",
  enableReinitialize: true,
  validate: ValidasiFilterTambahBarang,
})(FormTambahBarang);
// export default connect(maptostate)(FormTambahBarang);
const selector = formValueSelector("FormTambahBarang"); // <-- same as form name

export default connect((state) => {
  const {
    harga_atribut,
    no_seri,
    berat_stock,
    nama_barang,
    kadar_cetak,
    kadar_modal,
    kode_intern,
    nama_atribut,
    berat_atribut,
    berat_bandrol,
    berat_plastik,
    tag_id,
    berat_asli,
    harga_total_tmp,
    ongkos,
    kadar,
    berat,
    ongkos_berlian,
    profit,
    foto,
  } = selector(
    state,
    "harga_atribut",
    "harga_total_tmp",
    "ongkos",
    "kadar",
    "berat",
    "berat_asli",
    "ongkos_berlian",
    "profit",
    "nama_barang",
    "kode_intern",
    "kadar_cetak",
    "kadar_modal",
    "nama_atribut",
    "berat_atribut",
    "berat_plastik",
    "berat_bandrol",
    "tag_id",
    "no_seri",
    "berat_stock",
    "foto",
  );

  let kode = state.databarang.showEditBarang.kode_lokasi_toko.split("~");
  // this.props.
  // console.log('state');
  // console.log(state.databarang.showEditBarang.berat);
  let harga_skrg = getItem("data_group").harga;
  const harga_string = String((kadar / 100) * berat * harga_skrg);
  const harga_jual = parseInt(harga_string.split(".")[0]);
  const harga_modded = harga_jual % Number(getItem("pembulatan"));
  const harga_bulat = harga_modded !== 0 ? harga_jual - harga_modded + Number(getItem("pembulatan")) : harga_jual;

  let harga_total_tanpa_profit = parseInt(ongkos_berlian || 0) + parseInt(harga_bulat || 0) + Number(getItem("total_rp") || 0);

  let harga_total_profit = parseInt(harga_total_tanpa_profit || 0) + (parseInt(harga_total_tanpa_profit || 0) * parseFloat(profit || 0)) / 100;


  let calculate_harga =
    parseInt(harga_total_tmp) +
      parseInt(harga_atribut || 0) +
      parseInt(ongkos || 0) || 0;

  let kode_toko = getItem("kode_lokasi_toko");
  let kode_jenis = getItem("kode_jenis");
  let kode_group = getItem("kode_group");
  let dataEdit = {
    modal_berlian : "MODAL_BERLIAN",
    kode_barcode: "-",
    nama_barang: nama_barang,
    nama_atribut: nama_atribut || "-",
    harga_atribut: harga_atribut || 0,
    kode_intern: kode_intern,
    kadar: parseInt(kadar) || 0,
    no_seri: no_seri || "-",
    ongkos_berlian: parseInt(ongkos_berlian) || 0,
    profit: parseInt(profit) || 0,
    kadar_cetak: kadar_cetak || 0,
    kadar_modal: parseInt(kadar_modal) || 0,
    kode_kategori: kode_group,
    kode_jenis: kode_jenis,
    kode_lokasi_toko: kode_toko,
    berat: parseFloat(berat) ||0,
    berat_stock: parseFloat(berat_stock)|| 0 ,
    berat_asli: parseFloat(berat_asli),
    berat_atribut: parseFloat(berat_atribut) || 0,
    berat_plastik: parseFloat(berat_plastik) || 0,
    tag_id: tag_id || "-",
    berat_bandrol: parseFloat(berat_bandrol) || 0,
    total_harga: parseFloat(harga_total_profit) || 0,
    harga_jual: parseFloat(harga_bulat) || 0,
    foto: foto,

  };
  return {
    databarangs: dataEdit,
    DataBarang: state.databarang.showEditBarang,
    status: state.databarang.showEditBarang.status,
    getDataTimbangan: state.utility.getTimbangan,
    kode_group: state.databarang.showEditBarang.kode_kategori,
    harga_total: calculate_harga,
    ongkos: state.databarang.showEditBarang.ongkos || 0,
    harga_jual: harga_bulat || 0,
    total_harga: parseFloat(harga_total_profit),
    initialValues: {
      kode_kategori: state.databarang.showEditBarang.kode_kategori,
      ongkos_berlian: state.databarang.showEditBarang.ongkos_berlian,
      kode_jenis: state.databarang.showEditBarang.kode_jenis,
      kode_lokasi_toko: kode[0],
      kode_gudang: kode[1],
      berat_asli: state.databarang.showEditBarang.berat_asli,
      harga_jual: state.databarang.showEditBarang.harga_jual || 0,
      kode_intern: state.databarang.showEditBarang.kode_intern,
      kode_barcode: state.databarang.showEditBarang.kode_barcode,
      nama_barang: state.databarang.showEditBarang.nama_barang,
      total_harga: state.databarang.showEditBarang.total_harga,
      ongkos: state.databarang.showEditBarang.ongkos || 0,
      berat_stock: state.databarang.showEditBarang.berat_stock || 0,
      kadar: state.databarang.showEditBarang.kadar,
      no_seri: state.databarang.showEditBarang.no_seri,
      profit: state.databarang.showEditBarang.profit,
      kadar_cetak: state.databarang.showEditBarang.kadar_cetak,
      kadar_modal: state.databarang.showEditBarang.kadar_modal,
      nama_atribut: state.databarang.showEditBarang.nama_atribut,
      berat_atribut: state.databarang.showEditBarang.berat_atribut,
      harga_atribut: state.databarang.showEditBarang.harga_atribut,
      tag_id: state.databarang.showEditBarang.tag_id || "-",
      berat:
        state.databarang.showEditBarang.status === "PESANAN"
          ? state.databarang.showEditBarang.berat
          : state.databarang.showEditBarang.berat,
      berat_awal:
        state.databarang.showEditBarang.status === "PESANAN"
          ? state.databarang.showEditBarang.berat
          : "-",
      berat_bandrol: state.databarang.showEditBarang.berat_bandrol,
      berat_plastik: state.databarang.showEditBarang.berat_plastik,
      foto: state.databarang.showEditBarang.foto,
      status_pesanan: state.databarang.showEditBarang.status,
      no_pesanan:
        state.databarang.showEditBarang.no_pesanan === undefined
          ? ""
          : state.databarang.showEditBarang.no_pesanan.trim(),
    },
    harga_atribut: state.databarang.showEditBarang.harga_atribut,
    foto: state.databarang.showEditBarang.foto,
    setCamera: state.databarang.setCamera,
    hideModal: state.datamaster.modalDialog,
  };
})(FormTambahBarang);
